"use client";

import { useLogout } from "@/hooks/use-logout";
import { useUser } from "@/hooks/use-user";
import socket from "@/socket-io";
import { use, useEffect, useState } from "react";

type ChatMessage = {
  type: string;
  content: string;
  sender: number;
  receiver: number;
};

export default function Home() {
  const [data, setData] = useState<ChatMessage[]>([]);
  const [message, setMessage] = useState("");
  const user = useUser();

  useEffect(() => {
    // Listen for incoming messages
    socket.on("newMessage", (incomingMessage: ChatMessage) => {
      setData((prev) => [...prev, incomingMessage]);
    });

    // Clean up the socket listener on component unmount
    return () => {
      socket.off("newMessage");
    };
  }, []); // Add empty dependency array to ensure the listener is added only once

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    if (!message.trim()) return; // Prevent empty messages
    if (!user) {
      console.error("User is not logged in");
      return;
    }

    const messageData: ChatMessage = {
      type: "text",
      content: message,
      sender: user.id,
      receiver: user.id, // Adjust this as per actual recipient logic
    };

    socket.emit("sendMessage", messageData); // Send message to the server
    setMessage(""); // Clear input after sending
  };
  const logout = useLogout();

  const handleLogout = () => {
    logout();
  };

  return (
    <div className="p-4">
      <h1 className="text-xl font-bold">Chat Application</h1>
      <div className="my-4">
        {user ? (
          <div>
            <p>Username: {user.username}</p>
            <p>Email: {user.email}</p>

            <button onclick={handleLogout}>logout</button>
          </div>
        ) : (
          <p>Please log in to start chatting.</p>
        )}
      </div>

      {user ? (
        <form onSubmit={handleSubmit} className="mb-4">
          <label htmlFor="message" className="block mb-2 text-sm font-medium">
            Message
          </label>
          <input
            type="text"
            name="message"
            placeholder="Type your message"
            className="w-full p-2 border border-gray-300 rounded-md text-black"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
          />
          <button
            type="submit"
            className="mt-2 px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600"
          >
            Send Message
          </button>
        </form>
      ) : (
        <p className="text-red-500">Login to send messages.</p>
      )}

      <h2 className="text-lg font-semibold">Chat Messages</h2>
      <div className="mt-4">
        {data.length > 0 ? (
          data.map((msg, index) => (
            <div key={`${msg.sender}-${index}`} className="mb-2">
              <span className="font-bold">{msg.sender}</span>: {msg.content}
            </div>
          ))
        ) : (
          <p>No messages yet.</p>
        )}
      </div>
    </div>
  );
}
